import React, {Component} from 'react'
import Auth from '../modules/Auth'
import {GridList, GridTile, IconButton, LinearProgress, Snackbar, Subheader} from 'material-ui'
import FloatingActionButton from 'material-ui/FloatingActionButton'
import ContentAdd from 'material-ui/svg-icons/content/add'
import ContentRemove from 'material-ui/svg-icons/content/remove'
import {withRouter} from 'react-router-dom'

const style = {
    top: 'auto',
    right: 20,
    bottom: 20,
    left: 'auto',
    position: 'fixed'
}

export default withRouter(class Albums extends Component {

    constructor(props) {
        super(props)
        this.state = {
            searchTitle: '',
            albums: [],
            loading: true
        }
        this.getAlbums = this.getAlbums.bind(this)
        this.handleClose = this.handleClose.bind(this)
    }

    componentWillMount() {
        this.getAlbums()
    }

    getAlbums() {
        fetch('/albums/all', {
            method: 'post',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `bearer ${Auth.getToken()}`
            },
            body: JSON.stringify({
                searchTitle: this.state.searchTitle
            })
        })
            .then(res => {
                if(res.status === 200)
                    return res.json()
                else
                    this.setState({ serverError: res.statusText, loading: false })
            })
            .then(json => {
                if(json.error)
                    this.setState({ serverError: JSON.stringify(json.error), loading: false })
                else
                    this.setState({ albums: json.albums, loading: false })
            })
            .catch(err => this.setState({ serverError: err, loading: false }))
    }

    handleClose() {
        this.setState({ serverError: null })
    }

    render() {
        return (
            <div>
                {this.state.loading &&
                <LinearProgress/>
                }
                {!this.state.loading &&
                <div>
                    <FloatingActionButton
                        style={style}
                        zDepth={2}
                    >
                        <ContentAdd/>
                    </FloatingActionButton>
                    <GridList
                        cellHeight={180}
                    >
                        <Subheader>Guns N' Roses Albums</Subheader>
                        {this.state.albums.map(album => (
                            <GridTile
                                key={album.id}
                                title={album.title}
                                onClick={(e) => this.props.history.push('/album/' + album.id)}
                                actionIcon={<IconButton><ContentRemove color="white" /></IconButton>}
                            >
                                <img src={`data:image;base64,${Buffer.from(album.image).toString('base64')}`} />
                            </GridTile>
                        ))}
                    </GridList>
                    <Snackbar
                        open={!!this.state.serverError}
                        message={this.state.serverError || ''}
                        autoHideDuration={4000}
                        onRequestClose={this.handleClose}
                    />
                </div>
                }
            </div>
        )
    }
})


