import React, {Component} from 'react'
import Auth from '../modules/Auth'
import {LinearProgress, Snackbar} from 'material-ui'

export default class Album extends Component {

    constructor(props) {
        super(props)
        this.state = {
            id: this.props.match.params.id,
            loading: true
        }
        this.getAlbums = this.getAlbums.bind(this)
        this.handleClose = this.handleClose.bind(this)
    }

    componentWillMount() {
        this.getAlbums()
    }

    getAlbums() {
        fetch(`/albums/all/${this.state.id}`, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `bearer ${Auth.getToken()}`
            }
        })
            .then(res => {
                if(res.status === 200)
                    return res.json()
                else
                    this.setState({ serverError: res.statusText, loading: false})
            })
            .then(json => {
                console.log(JSON.stringify(json))
                if (json.error) {
                    this.setState({serverError: JSON.stringify(json.error), loading: false})
                } else {
                    const newState = json
                    newState.loading = false
                    this.setState(newState)
                }
            })
    }

    handleClose() {
        this.setState({ serverError: null })
    }

    render() {
        return (
            <div>
                {this.state.loading &&
                    <LinearProgress/>
                }
                {!this.state.loading &&
                <Snackbar
                    open={!!this.state.serverError}
                    message={this.state.serverError || ''}
                    autoHideDuration={4000}
                    onRequestClose={this.handleClose}
                />
                }
            </div>
        )
    }
}


